<template>
  <div class="knowledgeArticle">
    <Header>
      <template #back>
        <img class="back" @click="back()" src="@/assets/back.png" alt />
      </template>
      <template #title>
        <h4 class="title">{{list.name}}</h4>
      </template>
    </Header>
    <NavBar :list="list" @func="getChildrenId"></NavBar>
    <div class="scrollWrapper" ref="scrollWrapper">
      <ArticleList v-for="(item, index) in articleList" :key="index" :list="item" :id="item.id"></ArticleList>
      <ArticleList v-for="(item, index) in beforeArticle" :key="'before'+index" :list="item" :id="item.id"></ArticleList>
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header/Header";
import NavBar from "@/components/NavBar/NavBar";
import ArticleList from "@/components/ArticleList/ArticleList";
export default {
  data() {
    return {
      list: null, //knowledge
      pageNum: 1,
      articleList: [], //listcid
      cid: null,
      beforeArticle: []
    };
  },
  components: {
    Header,
    NavBar,
    ArticleList
  },
  created() {
    this.setParams();
    this.getArticle(this.list.children[0].id); //id
  },
  methods: {
    //data
    setParams() {
      // this.list = this.$route.params; listlist
      this.list = JSON.parse(sessionStorage.getItem('list'));//json
    },
    //id
    getArticle(id) {
      this.axios
        .get(`/article/list/0/json?cid=${id}`)
        .then(res => {
          this.articleList = res.data.data.datas;
        })
        .catch(error => error);
    },
    //
    getBeforeArticle(id) {
      this.axios
        .get(`/article/list/${this.pageNum}/json?cid=${id}`)
        .then(res => {
          this.beforeArticle.push(...res.data.data.datas);
        })
        .catch(error => error);
      this.pageNum++;
    },
    //
    handleScroll() {
      let wrapper = this.$refs.scrollWrapper;
      if (this.cid == null) {
        this.cid = this.list.children[0].id;
      }
      if (wrapper.scrollHeight - wrapper.scrollTop === wrapper.clientHeight) {
        this.getBeforeArticle(this.cid);
      }
    },
    getChildrenId(data) {
      this.cid = data;
    },
    //
    clear() {
      this.beforeArticle.length = 0;
      this.pageNum = 1;
    },
    back() {
      this.$router.go(-1);
    }
  },
  mounted() {
    this.$refs.scrollWrapper.addEventListener("scroll", this.handleScroll);
  }
};
</script>

<style lang="scss">
.knowledgeArticle {
  height: 100vh;
  background-color: #b2bec3;
  .scrollWrapper {
    height: 87vh;
    overflow: scroll;
  }
}
</style>
